:root{
    --section-overlay:rgba(0,0,0,0.7);
    --background-overlay:rgba(0,0,0,0.2);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}
.Weather{
    
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: calc(80rem);
    background-image: url("../Asset/cold.jpg");
}
.overlay{
    width: 100%;
    height: 100vh;
    background-color: var(--background-overlay);
}
.container{
    max-width: 800px;
    margin: auto;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction:column;
    padding: 1rem;
}
.section{
    width: 100%;
    padding: 1rem;
    border-radius: 0.4rem;
    color: white;
}

.section-inputs, .section_temperature{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--section-overlay);
}
.section-inputs>input{
border: .8px solid white;
border-radius: .4rem;
background-color: transparent;
color: white;
padding: .5rem;
font-size: 20px;
font-weight: 200;
}
.section-inputs>input:focus{
    outline: none;
}
.section-inputs>button{
    padding: 10px 50px;
    border: none;
    border-radius: .5rem;
    font-size: 20px;
    font: 500;
    background-color: white;
}
.section-inputs>button:hover{
    cursor: pointer;
    background-color: lightgray;
}
.icon{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.icon>h3{
    font-size: 15px;
    font-weight: 200;
    text-transform: capitalize;
}
.icon img{
    height: 4rem;
}
.temperature>h1{
    font-size: 64px;
}